<template>
    <v-container id="template">
        <v-btn  id="print" class="blue white--text mr-4 mb-5" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="red white--text mb-5" to="/cetakkemirikm" link>Back</v-btn>
        <p id="print">Catatan : <br> *Kertas Print = 155 x 250 mm <br> *Orientasi = Portrait</p>
        <template id="template">
            <div class="grid-container">
                <template v-for="i in karton">     
                    <div v-for="j in i['Jumlah']" :key="j.id" class="grid-item">
                        <table style="width: 55px;height: 90px;" cellspacing=0>
                            <tr>
                                <td>
                                    <div id="barcode">
                                        <img class="qr" :src="$publicUrl + i['pathQR']" >
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <div id="namaBarang"><b> BPOM RI {{ i['NoEdar']}}</b></div>
                                </td>
                            </tr>
                            <tr>
                                <td><b>Nama Brg : {{ i['Kode Barang'] }}</b></td>
                            </tr>
                            <tr>
                                <td><b>No Bets : {{ i['No Bets']}}</b></td>
                            </tr>
                            <tr>
                                <td><b>Exp Date :{{ i['Expired Date']}}</b></td>
                            </tr>
                            <tr>
                                <td><b>Netto {{ i['Isi'] }} ml</b></td>
                            </tr>
                        </table>
                    </div>
                </template>
            </div>
        </template>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                karton: [],
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'cetakkemirikm/cetak'
                this.$http.get(uri).then(response => {
                    this.karton = response.data
                    console.log(this.karton)
                })
            },

            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style scoped>
    @media print {
        #print {
            display: none;
        }

        #template {
            background-color: white
        }
      
    }
   .grid-container{
            /* column-count: 3; */
            column-gap: 1.7cm;
            row-gap: 1.295cm;
            display: grid;
            grid-template-columns: repeat(4, 1.801cm);
            font-size: 4pt;
            font-family: "Arial", Helvetica, sans-serif;
            text-align: center;
            /* margin-top: 7mm;
            margin-bottom: 7mm;
            margin-left: 17mm;
            margin-right:10mm; */
        }

        .grid-item{
            height: 4.861cm;
            width: 1.802cm;
        }
        #barcode {
            background-color: lightgrey;
            width: 0.7cm;
            height:0.7cm;
            /* margin-left: 0.582cm; */
            margin-left: 0.552cm;
        }
        #namaBarang{
            margin-top: 3.101cm;
            width: 1.801cm;
            height: 0.21cm;
        }
        .qr{
    
            width: 0.7cm;
            height: 0.7cm;
            -webkit-print-color-adjust: exact; 
        }
</style>